'use client';

import dynamic from 'next/dynamic';

import { motion } from 'framer-motion';

import '@/components/Hero.css';

interface HomeInfo {
  encabezado_1: string;
  encabezado_2: string;
  encabezado_3: string;
  descripcion: string;
  tituloMultimedia: string;
  video: string;
}

interface HeroProps {
  homeInfo: HomeInfo;
}

const banner = {
  animate: {
    transition: {
      delayChildren: 0.4,
      staggerChildren: 0.1,
    },
  },
} as const;

const letterAnimation = {
  initial: { y: 400 },
  animate: {
    y: 0,
    transition: {
      ease: [0.6, 0.01, -0.05, 0.95],
      duration: 1,
    },
  },
};

export const Hero = ({ homeInfo }: HeroProps) => {
  const { encabezado_1, encabezado_2, encabezado_3, tituloMultimedia, video } =
    homeInfo;

  return (
    <section className="mx-auto relative flex flex-col border-2 border-red-600 max-w-4xl">
      <div className="flex justify-center w-full">
        <div className="banner section flex flex-col grow max-w-5xl">
          <BannerRow title={encabezado_1} />
          <BannerRow title={encabezado_2} delay={1} className="self-center" />
          <BannerRow title={encabezado_3} delay={2} className="self-end" />
        </div>
      </div>

      <motion.div
        initial={{ opacity: 0, y: 80 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{
          ease: [0.76, 0, 0.24, 1],
          duration: 1,
          delay: 1.4,
        }}
        className="mt-10 aspect-video w-[840px] self-center z-10"
      >
        <video
          controls
          preload="metadata"
          className="rounded-lg border-6 border-black"
        >
          <source
            src={video}
            type="video/mp4"
            className="w-full aspect-video"
          />
        </video>
      </motion.div>
    </section>
  );
};

const BannerRow = ({
  title,
  className,
  delay,
}: {
  title: string;
  className?: string;
  delay?: number;
}) => {
  return (
    <div className={`banner-row${className ? ` ${className}` : ''}`}>
      <AnimatedLetters title={title} delayNumber={delay} />
    </div>
  );
};

const AnimatedLetters = ({
  title,
  disabled,
  delayNumber,
}: {
  title: string;
  disabled?: boolean;
  delayNumber?: number;
}) => {
  return (
    <motion.h1
      className="row-title"
      variants={disabled ? undefined : banner}
      initial="initial"
      animate="animate"
      transition={{ duration: 1, delay: delayNumber }}
    >
      {[...title].map((letter, key) => (
        <motion.span
          key={key}
          className="row-letter"
          variants={disabled ? undefined : letterAnimation}
        >
          {letter}
        </motion.span>
      ))}
    </motion.h1>
  );
};
