'use client';

// app/page.tsx (o donde se encuentra tu archivo Home)
import { motion } from 'framer-motion';

import { Background } from '@/components/Background';
import { Header } from '@/components/Header';
import { Hero } from '@/components/Hero';
import { getHomeInfo } from '@/lib/get_home_info';

// Tipos de los datos que vas a recibir
interface HomeInfo {
  encabezado_1: string;
  encabezado_2: string;
  encabezado_3: string;
  descripcion: string;
  tituloMultimedia: string;
  video: string;
}

// Función asíncrona para obtener los datos antes de renderizar la página
const fetchHomeInfo = async (): Promise<HomeInfo> => {
  try {
    const homeInfo = await getHomeInfo();
    if (!homeInfo) {
      throw new Error('No se pudo obtener la información.');
    }
    return homeInfo;
  } catch (error) {
    console.error('Error al cargar la información del home:', error);
    return {
      encabezado_1: '',
      encabezado_2: '',
      encabezado_3: '',
      descripcion: '',
      tituloMultimedia: '',
      video: '',
    };
  }
};

export default async function Home() {
  const homeInfo = await fetchHomeInfo();

  return (
    <main>
      {/* <Background /> */}
      <Header />
      <div className="max-w-[1400px] mx-auto border-2 border-slate-600">
        <Hero homeInfo={homeInfo} />
        <motion.p
          initial={{ opacity: 0, y: 40 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{
            ease: 'easeInOut',
            duration: 1,
            delay: 1.2,
          }}
          className="absolute text-xl top-24 right-24 max-w-[380px]"
        >
          {homeInfo.descripcion}
        </motion.p>
      </div>
    </main>
  );
}
