import { query } from './strapi';

export function getHomeInfo() {
  const { STRAPI_HOST } = process.env;
  return query(
    'pagina-principal?fields[0]=encabezado_1&fields[1]=encabezado_2&fields[2]=encabezado_3&fields[3]=descripcion&populate[videoCover][fields][0]=url',
  ).then((res) => {
    const {
      tituloMultimedia,
      descripcion,
      encabezado_1,
      encabezado_2,
      encabezado_3,
      videoCover,
    } = res.data;
    const video = `${STRAPI_HOST}${videoCover[0].url}`;

    return {
      tituloMultimedia,
      descripcion,
      encabezado_1,
      encabezado_2,
      encabezado_3,
      video,
    };
  });
}
